/* work together */
.workTogether{
    background-color: #1A191B;
    color: white;
    display: flex;
    flex-direction: column;
    padding: 10px 50px 10px 50px;
    text-align: center;
}
.workTogether h4{
    margin-top: 150px;
    font-weight: 400;
    letter-spacing: 5px;
    font-size: 15px;
}
.workTogether h1{
    margin-top: 0px;
    font-size: 90px;
    margin-bottom: 250px;
    z-index: 999;
}