@import url('https://fonts.googleapis.com/css2?family=Poppins&display=swap');

.about{
    display: flex;
    flex-direction: column;
    background-color: #1A191B;
    height: 100%;
    padding: 10px 50px 10px 50px;
    color: white;
}
.about ul{
    display: flex;
}
.about ul div{
    margin-right: -430px;
    /* backgroun9d-color: pink; */
}
.about h2{
    margin-top: 282px;
    font-size: 15px;
    font-weight: 400;
}
.about h1{
    margin-top: -5px;
    margin-bottom: 10px;
    font-size: 80px;
}
.about p:nth-child(3),
.about p:nth-child(4),
.about ul li{
    color:#888A8E;
    font-size: 20px;
    font-weight: 430;
    margin-right: 450px;
    line-height: 25px;
    filter: brightness(1.5);
}
.about p:nth-child(4){
    margin-bottom: 55px;
}

.about p{
    display: flex;
    text-align: justify;
    margin-top: 5px;
}
.about ul li{
    /* background-color: teal; */
    
    width: 350px;
}
.about ul div{
    margin-top: -15px;
    margin-bottom: 5px;
}