@import url('https://fonts.googleapis.com/css2?family=Lato&family=Poppins:wght@500&display=swap');

.top{
    display: flex;
    flex-direction: column;
    background-color: #1A191B;
    height: 100%;
    padding: 10px 50px 10px 50px;
    color: white;
}
/* Top Bar */
.bar{
    width: 100%; 
    height: 70px;
    display: flex;
}
.bar p{
    display: flex;
    font-size: 22px;
}
.bar p:nth-child(1){
    font-weight: bold;
}
.bar p:nth-child(2){
    margin-top: 25px;
    margin-right: 10px;
    font-weight: bold;
    font-size: 20px;
}
.bar p:nth-child(3){
    border: 1px solid;
    width: 7px;
    height: 7px;
    margin-top: 35px;
    color:#242526;
    border-radius: 100%;
    transition: all .2s ease-in-out;
}
.bar p:nth-child(1){
    display: flex;
    width: 100%;
}
/* decoration */
.decoration p{
    margin-left: -15px;
    margin-top: 10px;
    font-size: 250px;
    /* position: absolute; */
    font-weight: bold;
    font-family: 'Poppins', sans-serif;
    color: transparent;
    /* color: #1A191B; */
    text-shadow: 0px 0px 6px #1A191B;
    filter: brightness(1.1);
    z-index: -999;
}
/* content */
.content{
    display: flex;
    flex-direction: column;
    align-items: flex-start;
}
.content h2{
    margin-top: -280px;
    letter-spacing: 3px;
    font-weight: 300;
    font-size: 15px;
    color:#888A8E;
}
.content h1{
    margin-top: 0px;
    font-size: 70px;
}
.content p{
    color:#888A8E;
    margin-top: 5px;
    font-size: 20px;
    font-weight: 400;
}
.content p{
    font-size: 19px;
}
.content p:nth-child(3){
    margin-top: -30px;
}
.content p:nth-child(4),
.content p:nth-child(5){
    margin-top: -10px;
    display: flex;
    text-align: center;
}
.content p:nth-child(6){
    margin-top: 10px;
    margin-bottom: 108px;
}
.content p b{
    color: white;
}
/* + animation + */
.bar p:nth-child(3):hover{
    transform: scale(2);
}

