.navcontact{
    display: flex;
    justify-content: center;
    margin-bottom: -10px;
}
.navConContainer{
    display: flex;
    background-color: #242526;
    justify-content: center;
    text-align: center;
    width: 99.5%;
    border-top-right-radius: 20px;
    border-top-left-radius: 20px;
    flex-direction: column;
    color: white;
}
.navConContainer div{
    display: flex;
    margin: 0px auto;
}
.navConContainer div p{
    margin-top: 21px;
    margin-left: 10px;
    display: flex;
    flex-direction: row;
    align-items: baseline;
}
.navConContainer span{
    font-size: 16px;
    margin-left: 5px;
}
.navConContainer p{
    padding-top: 2px;
    font-size: 16px;
    font-weight: 500;
}
.navConContainer2{
    width: 60%;
}
.navConContainer .copyright{
    margin-top: -30px;
    display: flex;
    text-align: center;
    justify-content: center;
    padding-right: 20px;
}
.navConContainer .copyright p{
    font-size: 10px;
}