.posts-container{
    margin-top: 50px;
   display: flex;
   background-color: #E5E5E5;
   margin-left: 50px;
   margin-right: 50px; 
   border-radius: 10px;
   flex-direction: column;
}
.posts-container h1{
    text-align: center;
}
.posts-container ul{
    padding-left: 0 0 0 0;
}
.posts-container ul li{
    padding-top: 10px;
    height: 100px;
}
.posts-container ul li:nth-child(2n-1){
    background-color: black;
    color: white;
}