.footer{
    background-color: #1A191B;
}
.foContainer{
    padding: 0 0 0 0;
    background-color: white;
    color: black;
    display: flex;
    flex-direction: column;
    padding: 10px 50px 10px 50px;
    text-align: center;
    justify-content: center;
    width: 30%;
    margin: -130px auto;
    border-top-right-radius: 10px;
    border-top-left-radius: 10px;
}
.foContainer span{
    margin-left: -50px;
    width: 124%;
    height: 1px;
    background-color: black;
}
.foContainer p{
    font-weight: 600;
}
.foContainer p:nth-child(4),
.foContainer p:nth-child(5){
    margin-top: 15px;
    font-size: 10px;
}
.foContainer p:nth-child(5){
    margin-top: -5px;
}
.foContainer h2{
    display: flex;
    margin: 0px auto;
    justify-content: space-between;
    width: 100px;
    text-align: center;
    font-size: 25px;
}
