  #status{
    width: 90%;
    max-width: 500px;
    text-align: center;
    padding: 10px;
    margin: 0 auto;
    border-radius: 8px;
  }
  #status.success{
    background-color: rgb(211,250,153);
    -webkit-animation: status 4s ease forwards;
            animation: status 4s ease forwards;
  }
  #status.error{
    background-color: rgb(250,129,92);
    color: white;
    -webkit-animation: status 4s ease forwards;
            animation: status 4s ease forwards;
  }
  @-webkit-keyframes status {
    0%{
      opacity: 1;
      pointer-events: all;
    }
    90%{
      opacity: 1;
      pointer-events: all;
    }
    100%{
      opacity: 0;
      pointer-events: none;
    }
  }
  @keyframes status {
    0%{
      opacity: 1;
      pointer-events: all;
    }
    90%{
      opacity: 1;
      pointer-events: all;
    }
    100%{
      opacity: 0;
      pointer-events: none;
    }
