    border-radius: 15px;
    transition: 0.5s;
    color: white;
    padding-left: 50px;
    visibility:hidden;
    opacity: 0;
    position: sticky;
    top: 95px;
  }
  .menu-click a{
    text-decoration: none;
    color: white;
    line-height: 40px;
  }
  .menu-click.active{
    visibility: visible;
