@import url('https://fonts.googleapis.com/css2?family=Lato&family=Poppins:wght@500&display=swap');

.myProjects{
    /* background-color: white; */
    padding: 50px 50px 10px 50px;
}
.myProjects .projContainer{
    padding-bottom: 30px;
}
.mid{
    display: flex;
    flex-direction: column;
    /* background-color: white; */
    background-color: #1A191B;
    padding: 50px 50px 10px 50px;
    color: black;
}
.projContainer{
    padding: 1px 0px 40px 0px;
    background-color: #E5E5E5;
    margin-bottom: 50px;
    border-radius: 10px;
}
.project h2{
    letter-spacing: 5px;
    margin-top: 30px;
    display: flex;
    justify-content: center;
    font-size: 30px;
    font-weight: 800;
}
.project h3:nth-child(2),
.project h3:nth-child(3){
    margin-top: -10px;
    display: flex;
    justify-content: center;
    font-weight: 500;
}
.project ul{
    padding: 0 0 0 0;
    margin-top: 40px;
    display: flex;
    /* border-style: solid;  */
    border-radius: 10px;
}
.project ul li{
    padding: 10px 30px 30px 30px;
    list-style-type: none;
    margin: -15px auto;
    width: 380px;
    /* height: 450px; */
    background-color: #1A191B;
    border-radius: 5px;
    color: white;
    transition: all .1s ease-in-out;
    border: 1px solid silver;
}
.project ul:nth-child(1){
    margin-bottom: -70px;
}
.project ul:nth-child(3){
    margin-top: 60px;
}
.project ul li p{
    color:#888A8E;
    margin-top: -10px;
    display: flex;
    text-align: left;
    font-family: 'Lato', sans-serif;
    line-height: 20px;
    filter: brightness(1.5);
}
.project ul li p b{
    margin-top: 5px;
    margin-bottom: -15px;
    display: inline-flex;
}
.project ul li img{
    width: 380px;
    border-radius: 5px;
}
/* animation */
.project ul li:hover{
    transform: scale(1.02);
}
.projContainer1{
    padding: 1px 0px 45px 0px;
    background-color: #E5E5E5;
    margin-bottom: 50px;
    border-radius: 20px;
    height: 375px;
}

